import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import TableHead from "@mui/material/TableHead";

const dispatchedEvents = [];

const SrsMuiTableHead = function (props, ref) {
  return (
    <TableHead {...props} theme={props.muiTheme}>
      <slot />
    </TableHead>
  );
};

const styles = `
  /* Custom styles for the MuiTableHead component */
`;

const props = [
  {
    name: "component",
    defaultValue: "thead",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiTableHead),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
